
import { Options, Vue } from "vue-class-component";

import { TransportEnum } from "@/types/graphql";

@Options({
  props: {
    available: Array<TransportEnum>
  },

  emits: ["select"]
})
export default class AvailableMethodSelector extends Vue {
  get METHODS_LABELS(): Record<TransportEnum, string> {
    return {
      [TransportEnum.Email]: "Email",
      [TransportEnum.Sms]: "Text",
    }
  }
}
